import { getShipAddress, getBillAddress} from './../../reducers/selectors';
import { AppState } from './../../../interfaces';
import { Store } from '@ngrx/store';
import { AddressService } from './../../../core/services/address.service';
import { CheckoutService } from './../../../core/services/checkout.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-address-form',
  templateUrl: './address-form.component.html',
  styleUrls: ['./address-form.component.scss']
})
export class AddressFormComponent implements OnInit {
  addressForm: FormGroup;
  countries: any;
  states: any;
  @Input() addressData: any;
  @Input() address: any;
  @Output() onChangeAddr = new EventEmitter();
  
  constructor(
    private fb: FormBuilder,
    private checkoutService: CheckoutService,
    private addrService: AddressService,
    private store: Store<AppState>) {
  }

  ngOnInit() {
    this.addressForm = this.initAddressForm(this.address);
    this.getCountries();
    this.getStates(this.address.country_id);
    this.onChanges();
    this.onSumbitInitValue();
    
  }

  onSubmit() {
  }

  ngOnDestroy() {
  }

  initAddressForm(address: any) {
    if (address)
      return this.fb.group({
        'firstname': [address.firstname, Validators.required],
        'lastname': [address.lastname, Validators.required],
        'address1': [address.address1, Validators.required],
        'address2': [address.address2],
        'city': [address.city, Validators.required],
        'phone': [address.phone, Validators.required],
        'zipcode': [address.zipcode, Validators.required],
        'state_id': [address.state_id, Validators.required],
        'country_id': [address.country_id, Validators.required]
      });
    else
      return this.fb.group({
        'firstname': ['', Validators.required],
        'lastname': ['', Validators.required],
        'address1': ['', Validators.required],
        'address2': [''],
        'city': ['', Validators.required],
        'phone': ['', Validators.required],
        'zipcode': [10001, Validators.required],
        'state_id': [3561, Validators.required],
        'country_id': [232, Validators.required]
      });
  }
  onSumbitInitValue(){
    this.onChangeAddr.emit({
      isValid: this.addressForm.valid,
      value: this.addressForm.value
    });
  }
  onChanges(): void{
    this.addressForm.valueChanges.subscribe(val => {
      this.onChangeAddr.emit({
        isValid: this.addressForm.valid,
        value: val
      });
    });
  }

  createAddresAttributes(address) {
    return {
      'order': {
        'bill_address_attributes': address,
        'ship_address_attributes': address
      }
    };
  }

  getCountries() {
    this.countries = $.map(this.addressData, function(ct){
      let str = {
        id: ct.id,
        text: ct.name
      }
      return str;
    });
  }

  getStates(country_id: number) {
    let ct;
    for (let index in this.addressData){
      if (this.addressData[index].id == country_id ){
        ct = this.addressData[index];
        break;
      }
    }
    this.states = $.map(ct.states, function(st){
      let str = {
        id: st.id,
        text: st.name
      }
      return str;
    });
  }

  countryChanged(e: any){
    this.addressForm.controls['country_id'].setValue(e.value);
    this.getStates(e.value);
    (this.states.length > 0) ?
      this.addressForm.controls['state_id'].setValue(this.states[0].id) :
      this.addressForm.controls['state_id'].setValue(null);
  }
  stateChanged(e: any){
    this.addressForm.controls['state_id'].setValue(e.value);
  }
  
}



