import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DeliveryComponent } from './delivery.component';
import { ShipmentListComponent } from './components/shipment-list/shipment-list.component';
import { ShipmentItemComponent } from './components/shipment-list/shipment-item/shipment-item.component';
import { DeliverySummaryComponent } from './components/delivery-summary/delivery-summary.component';
import { ShipmentRatingComponent } from './components/shipment-list/shipment-rating/shipment-rating.component';

@NgModule({
  imports: [
    CommonModule
  ],
  declarations: [
    DeliveryComponent,
    ShipmentListComponent,
    ShipmentItemComponent,
    DeliverySummaryComponent,
    ShipmentRatingComponent,
  ]
})
export class DeliveryModule { }



