import { Component, OnInit, Input, Output, OnDestroy, EventEmitter } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { AppState } from './../../../../../interfaces';
import { Shipment } from './../../../../../core/models/shipment';
import { ShippingRate } from './../../../../../core/models/shipping_rate';
import { getIsOwnShipFlag, getSelectedRateIndex } from './../../../../reducers/selectors';

@Component({
  selector: 'app-shipment-rating',
  templateUrl: './shipment-rating.component.html',
  styleUrls: ['./shipment-rating.component.scss']
})
export class ShipmentRatingComponent implements OnInit {
  @Input() shipmentItems: Shipment[];
  @Output() onSelected = new EventEmitter();
  shippingRates: ShippingRate[];
  selectedShippingRate: ShippingRate;
  selectedRateIndex: number;
  isOwnShipping: boolean;
  isOwnSub$: Subscription;
  selectedIndexSub$: Subscription;

  constructor(
    private store: Store<AppState>
  ) {
    this.isOwnSub$ = this.store.select(getIsOwnShipFlag)
      .subscribe( flag => {
        return this.isOwnShipping = flag;
      })
    this.selectedIndexSub$ = this.store.select(getSelectedRateIndex)
     .subscribe( index => {
       return this.selectedRateIndex = index;
     })
  }

  ngOnInit() {
    this.shippingRates = this.shipmentItems[0].shipping_rates;
    this.onSelected.emit(this.createSeletedAttributes());
  }

  createSeletedAttributes() {
    let selectedRates = [];
    this.shipmentItems.map(item => {
      selectedRates.push(
        {
          'selected_shipping_rate_id': item.shipping_rates[this.selectedRateIndex].id,
          'id': item.id,
          'is_own_shipping': this.isOwnShipping
        }
      );
    })
    return selectedRates;
  }

  handleChange(e) {
    this.isOwnShipping = !this.isOwnShipping;
    this.onSelected.emit(this.createSeletedAttributes());
  }

  onSelChange(index: number) {
    this.selectedRateIndex = index;
    this.onSelected.emit(this.createSeletedAttributes());
  }

  ngOnDestroy() {
    this.isOwnSub$.unsubscribe();
    this.selectedIndexSub$.unsubscribe();
  }

}



