import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { AppState } from './../../../../interfaces';
import { getTotalItemValue, getTotalCartItems, getTotalCartValue } from './../../../reducers/selectors';

@Component({
  selector: 'app-delivery-summary',
  templateUrl: './delivery-summary.component.html',
  styleUrls: ['./delivery-summary.component.scss']
})
export class DeliverySummaryComponent implements OnInit {
  total$: Observable<number>;
  itemTotal$: Observable<number>;
  totalItems$: Observable<number>;

  constructor(
    private store: Store<AppState>
  ) {
    this.total$ = this.store.select(getTotalCartValue);
    this.itemTotal$ = this.store.select(getTotalItemValue);
    this.totalItems$ = this.store.select(getTotalCartItems);
  }

  ngOnInit() {
  }

}



