import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Shipment } from './../../../../core/models/shipment';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { AppState } from './../../../../interfaces';
import { getShipment } from './../../../reducers/selectors';

@Component({
  selector: 'app-shipment-list',
  templateUrl: './shipment-list.component.html',
  styleUrls: ['./shipment-list.component.scss']
})
export class ShipmentListComponent implements OnInit {
  shipmentItems$: Observable<Shipment[]>;
  selectedRates: any[];
  @Output() onShipRatesSelected = new EventEmitter();

  constructor(
    private store: Store<AppState>
  ) {
    this.shipmentItems$ = this.store.select(getShipment);
  }

  ngOnInit() {
    this.selectedRates = [];
  }

  onRateSelected(selectedRate) {
    this.onShipRatesSelected.emit({'shipments_attributes': selectedRate});
  }
}



