import { Variant } from './variant';
/*
 * LineItem model
 * Detailed info http://guides.spreecommerce.org/developer/orders.html#line-items
 * Public API's http://guides.spreecommerce.org/api/line_items.html
 */

export class LineItem {
  id: number;
  quantity: number;
  price: number;
  single_display_amount: number;
  total: number;
  display_amount: number;
  variant_id: number;
  variant: Variant;
}

export class LightLineItem {
  id: number;
  single_display_amount: string;
  display_amount: string;
  total: number;
  adjustment_slug: string;
  product_name: string;
  product_slug: string;
  image_url: string;
}


