import { Component, OnInit, Input } from '@angular/core';
import { Store } from '@ngrx/store';
import { AppState } from './../../../../../interfaces';
import { Observable } from 'rxjs';
import { Shipment } from './../../../../../core/models/shipment';
import { LineItem } from './../../../../../core/models/line_item';
import { Manifest } from './../../../../../core/models/manifest';
import { getLineItems } from './../../../../reducers/selectors';
import { environment } from './../../../../../../environments/environment';

@Component({
  selector: 'app-ship-item',
  templateUrl: './ship-item.component.html',
  styleUrls: ['./ship-item.component.scss']
})
export class ShipItemComponent implements OnInit {
  @Input() shipItem: Shipment;
  manifestItems: Manifest[];
  lineItems$: Observable<LineItem[]>;
  number: string;
  rateName: string;
  rateCost: string;
  state: string;
  isDetail: boolean;

  constructor(
    private store: Store<AppState>
  ) {
    this.lineItems$ = this.store.select(getLineItems);
  }

  ngOnInit() {
    this.isDetail = false;
    this.manifestItems = this.shipItem.manifest;
    this.number = this.shipItem.number;
    this.rateName = this.shipItem.selected_shipping_rate.name;
    this.rateCost = this.shipItem.selected_shipping_rate.display_cost;
    this.state = this.shipItem.state;
  }

  getImage(lineItem: LineItem) {
    if (lineItem.variant.images[0])
      return environment.API_ENDPOINT + lineItem.variant.images[0].product_url;
    else
      return "assets/logo.png";
  }

  isConsolidated() {
    if (this.manifestItems.length > 1)
      return true;
    else
      return false;
  }

  convertDetail() {
    this.isDetail = !this.isDetail;
  }

}



