import { RouterModule } from '@angular/router';
// import { AddressService } from './services/address.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AddressComponent } from './address.component';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { DeliveryOptionsComponent } from './delivery-options/delivery-options.component';
import { BillAddressComponent } from './bill-address/bill-address.component';
import { ShipAddressComponent } from './ship-address/ship-address.component';
import { Select2Module } from 'ng2-select2';
import { AddressFormComponent } from './address-form/address-form.component';


@NgModule({
  declarations: [
    AddressComponent,
    DeliveryOptionsComponent,
    BillAddressComponent,
    ShipAddressComponent,
    AddressFormComponent
  ],
  exports: [],
  imports: [
    RouterModule,
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    Select2Module
  ],
  providers: [
  ]
})
export class AddressModule { }



