import { Component, OnInit, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { getShipAddress } from './../../reducers/selectors';
import { AppState } from './../../../interfaces';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-ship-address',
  templateUrl: './ship-address.component.html',
  styleUrls: ['./ship-address.component.scss']
})
export class ShipAddressComponent implements OnInit {
  shipAddress$: Observable<any>;
  isChecked: boolean;
  @Output() onChangeAddress = new EventEmitter();
  @Input() addrData: any;

  constructor(
    private store: Store<AppState>
  ) {
    this.shipAddress$ = this.store.select(getShipAddress);
  }

  ngOnInit() {
    this.isChecked = true;
    this.onChangeAddress.emit(false);
  }

  handleChange(e) {
    this.isChecked = !this.isChecked;
    if (this.isChecked) 
      this.onChangeAddress.emit(false);
  }

  onChangeAddr(value){
    this.onChangeAddress.emit(value);
  }
}



