import { getOrderState, getUpdateStatus } from './../../../reducers/selectors';
import { Router } from '@angular/router';
import { CheckoutService } from './../../../../core/services/checkout.service';
import { CheckoutActions } from './../../../actions/checkout.actions';
import { AppState } from './../../../../interfaces';
import { Store } from '@ngrx/store';
import { Subscription } from 'rxjs/Subscription';
import { Component, OnInit, Input, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-order-total-summary',
  templateUrl: './order-total-summary.component.html',
  styleUrls: ['./order-total-summary.component.scss']
})
export class OrderTotalSummaryComponent implements OnInit, OnDestroy {
  
  @Input() totalCartValue: number;
  stateSub$: Subscription;
  updateStatusSub$: Subscription;
  orderState: string;
  updateState: boolean;

  constructor(private store: Store<AppState>,
    private actions: CheckoutActions,
    private checkoutService: CheckoutService,
    private router: Router) {
    this.stateSub$ = this.store.select(getOrderState)
      .subscribe(state => this.orderState = state);
    this.updateStatusSub$ = this.store.select(getUpdateStatus)
      .subscribe(state => this.updateState = state);
  }

  ngOnInit() {
  }

  placeOrder() {
    if (this.orderState === 'cart') {
      this.checkoutService.changeOrderState()
        .do(() => {
          this.navigateNext();
        })
        .subscribe();
    } else {
      if (this.updateState)
        this.checkoutService.updateOrder()
          .do(() => {
            this.navigateNext();
          })
          .subscribe();
      else
        this.navigateNext();
    }
  }

  navigateNext() {
    this.router.navigate(['/checkout', 'delivery']);
  }

  navigateShopping() {
    this.router.navigate(['/home']);
  }

  ngOnDestroy() {
    this.stateSub$.unsubscribe();
    this.updateStatusSub$.unsubscribe();
  }
}



