import { Router } from '@angular/router';
import { CheckoutService } from './../../core/services/checkout.service';
import { getShipAddress, getOrderState, getOrderNumber } from './../reducers/selectors';
import { AppState } from './../../interfaces';
import { Store } from '@ngrx/store';
import { Address } from './../../core/models/address';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import { AddressService } from './../../core/services/address.service';

@Component({
  selector: 'app-address',
  templateUrl: './address.component.html',
  styleUrls: ['./address.component.scss']
})
export class AddressComponent implements OnInit, OnDestroy {

  stateSub$: Subscription;
  orderState: string;
  orderNumber$: Observable<string>;
  addressData$: Observable<any>;
  isBillAddrValid: boolean;
  isShipAddrValid: boolean;
  isEqual: boolean;
  billAddress: any;
  shipAddress: any;
  

  constructor(private store: Store<AppState>,
    private checkoutService: CheckoutService,
    private addrService: AddressService,
    private router: Router) {
      this.orderNumber$ = this.store.select(getOrderNumber);
      this.stateSub$ = this.store.select(getOrderState)
        .subscribe(state => {
          this.orderState = state;
        });
  }

  ngOnInit() {
    this.addressData$ = this.addrService.getCountries();
  }

  // get change or init of bill address

  onChangeBillAddr(addr) {
    this.isBillAddrValid = addr.isValid;
    this.billAddress = addr.value;
  }

  // get change or init of ship address

  onChangeShipAddr(addr) {
    if (addr) {           //not same as bill address
      this.isShipAddrValid = addr.isValid;
      this.shipAddress = addr.value;
      this.isEqual = false;
    } else {              // same as bill address
      this.isShipAddrValid = true;
      this.isEqual = true;
    }
  }

  // create address attributes to call API

  createAddressAttr() {
    if (this.isEqual)
      this.shipAddress = this.billAddress;
    return {
      'order': {
        'bill_address_attributes': this.billAddress,
        'ship_address_attributes': this.shipAddress
      }
    };
  }

  checkoutToDelivery() {
    if (this.orderState === 'address') {
      this.checkoutService.updateOrderAndState(this.createAddressAttr())
        .do(() => {
          this.router.navigate(['/checkout', 'payment'])
        })
        .subscribe();
    } else {
      this.router.navigate(['/checkout', 'payment']);
    }
  }

  ngOnDestroy() {
    this.stateSub$.unsubscribe();
  }
}



