import { Component, OnInit, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { getBillAddress } from './../../reducers/selectors';
import { AppState } from './../../../interfaces';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-bill-address',
  templateUrl: './bill-address.component.html',
  styleUrls: ['./bill-address.component.scss']
})
export class BillAddressComponent implements OnInit {
  billAddress$: Observable<any>;
  @Output() onChangeAddress = new EventEmitter();
  @Input() addrData: any;

  constructor(
    private store: Store<AppState>
  ) {
    this.billAddress$ = this.store.select(getBillAddress);
  }

  ngOnInit() {
  }

  onChangeAddr(value){
    this.onChangeAddress.emit(value);
  }
}



