import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { AppState } from './../../interfaces';
import { Shipment } from './../../core/models/shipment';
import { Subscription } from 'rxjs/Subscription';
import { CheckoutService } from './../../core/services/checkout.service';
import { getOrderState, getOrderNumber } from './../reducers/selectors';


@Component({
  selector: 'app-delivery',
  templateUrl: './delivery.component.html',
  styleUrls: ['./delivery.component.scss']
})
export class DeliveryComponent implements OnInit {
  stateSub$: Subscription;
  orderState: string;
  shipAttributes: {};

  constructor(
    private store: Store<AppState>,
    private checkoutService: CheckoutService,
    private router: Router
  ) {
    this.stateSub$ = this.store.select(getOrderState)
      .subscribe(state => {
        this.orderState = state;
      });
  }

  ngOnInit() {
  }

  onSelected(attributes){
    this.shipAttributes = attributes;
  }

  checkoutToPayment(){
    if (this.orderState === 'delivery') {
      this.checkoutService.updateOrderAndState({'order': this.shipAttributes})
        .do(() => {
          this.router.navigate(['/checkout', 'address'])
        })
        .subscribe();
    } else {
      this.router.navigate(['/checkout', 'address']);
    }    
  }
}



