import { EffectsModule } from '@ngrx/effects';
import { CommonModule } from '@angular/common';
import { CartComponent } from './cart.component';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { LineItemListComponent } from './components/line-item-list/line-item-list.component';
import { LineItemComponent } from './components/line-item-list/line-item/line-item.component';
import { OrderTotalSummaryComponent } from './components/order-total-summary/order-total-summary.component';
import { EmptyCartComponent } from './components/empty-cart/empty-cart.component';
import { ShipItemListComponent } from './components/ship-item-list/ship-item-list.component';
import { ShipItemComponent } from './components/ship-item-list/ship-item/ship-item.component';

@NgModule({
  declarations: [
    CartComponent,
    LineItemListComponent,
    LineItemComponent,
    OrderTotalSummaryComponent,
    EmptyCartComponent,
    ShipItemListComponent,
    ShipItemComponent
  ],
  exports: [],
  imports: [
    CommonModule,
    RouterModule
  ],
  providers: []
})
export class CartModule { }



